import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {ScriptService} from "../script.service";
import {ReactiveFormsModule} from "@angular/forms";
import {Observable, of} from "rxjs";

@Component({
  selector: 'app-sceditor',
  imports: [
    ReactiveFormsModule
  ],
  templateUrl: './sceditor.component.html',
  styleUrl: './sceditor.component.css'
})
export class SceditorComponent implements OnInit, OnChanges {
  @Input('id') id: string  = ''
  @Input('format') format: string = 'xhtml'
  @Input('toolbar') toolbar: string;
  @Input('height') height: string|number;
  @Input('content') content: Observable<string|null> = of(null);
  private scripts: any[] = []
  private scriptService: ScriptService;

  constructor() {
    this.height = 300;
    this.toolbar = 'bold,italic,underline,strike,subscript,superscript|' +
        'left,center,right,justify|' +
        'font,size,color|' +
        'removeformat|' +
        'cut,copy,paste|' +
        'pastetext,bulletlist,orderedlist,table,code,quote|' +
        'horizontalrule|' +
        'image,link,unlink|' +
        'emoticon'
    let format_script = 'https://cdn.jsdelivr.net/npm/sceditor@3/minified/formats/xhtml.min.js'
    if (this.format == 'bbcode') {
      format_script = 'https://cdn.jsdelivr.net/npm/sceditor@3/minified/formats/bbcode.min.js'
    }
    this.scripts = [
      { name: 'main', src: 'https://cdn.jsdelivr.net/npm/sceditor@3/minified/sceditor.min.js' },
      { name: 'format', src: format_script }
    ];
    this.scriptService = new ScriptService(this.scripts);
  }

  ngOnInit() {
    this.scriptService.load('main', 'format').then(() => {
      const textarea = document.getElementById(this.id);
      // @ts-ignore
      sceditor.create(textarea, {
        format: this.format,
        toolbar: this.toolbar,
        style: 'https://cdn.jsdelivr.net/npm/sceditor@3/minified/themes/content/default.min.css',
        height: this.height,
        emoticonsRoot: 'https://www.sceditor.com/',

      });
    })
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if(changes['content']) {
      this.content.subscribe((content) => {
        if(content == null) {return}
        const textarea = document.getElementById(this.id);
        const valueWithLineBreaks = content.replace(/\n/g, "\r\n");
        // @ts-ignore
        return sceditor.instance(textarea).val(valueWithLineBreaks);
      })
    }
  }
}
